import { useState } from 'react';
import { routes } from 'utils/routes';
import logo from '../../utils/logo.png';
import ButtonFunc from '../../components/ButtonFunc';


import { HeaderStyled, Container, Logo, Nav, MenuLink, DropMenu, DropdownMenu, DropMenuItem } from './Header.styled';

export const Header = () => {
  const [isProjectsMenuOpen, setIsProjectsMenuOpen] = useState(false);
  const [isAboutMenuOpen, setIsAboutMenuOpen] = useState(false);

  const handleProjectsMenuToggle = () => {
    setIsProjectsMenuOpen(!isProjectsMenuOpen);
  };

  const handleAboutMenuToggle = () => {
    setIsAboutMenuOpen(!isAboutMenuOpen);
  };

  return (
    <HeaderStyled>
      <Container>
        <Logo to={routes.HOME} end>
          <img src={logo} alt="Company logo" />
        </Logo>
        <Nav>
          <DropMenu>
            <div onClick={handleAboutMenuToggle} onFocus={() => null}> </div>
            {isAboutMenuOpen && (
              <DropdownMenu>
                <DropMenuItem to={routes.HISTORY}>   </DropMenuItem>
                <DropMenuItem to={routes.TEAM}>  </DropMenuItem>
                <DropMenuItem to={routes.DOCS}>  </DropMenuItem>
              </DropdownMenu>
            )}
          </DropMenu>
          <DropMenu>
            <div onClick={handleProjectsMenuToggle} onFocus={() => null}></div>
            {isProjectsMenuOpen && (
              <DropdownMenu>
                <DropMenuItem to={routes.CURRENT}> </DropMenuItem>
                <DropMenuItem to={routes.SUCCESS}> </DropMenuItem>
              </DropdownMenu>
            )}
          </DropMenu>
          <MenuLink to={routes.CONTACTS}></MenuLink>
        </Nav>
        <a href="#button_help">
          <ButtonFunc class="hero-button">
            
          </ButtonFunc>
        </a>
      </Container>
    </HeaderStyled>
  );
};
